.sidebar {
  width: 250px;
  background-color: #6D2E46; /* wine color */
  color: white;
  padding: 20px;
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.sidebar-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  border-radius: 50%;
}

.logo-img {
  width: 150px;
  height: auto;
  object-fit: contain;
  box-shadow: 0 0 10px #6D2E46;
}

.logo-img:hover{
  transform:rotate(180deg);
  transition: 2s;
}

.sidebar-title {
  font-size: 16px;
  font-weight: bold;
  line-height: 1.5;
  margin-bottom: 40px;
  text-align: center;
}

.sidebar-title:hover{
  font-style: italic;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
}

.sidebar-menu li {
  margin-bottom: 20px;
  cursor: pointer;
  padding: 10px 12px;
  border-radius: 8px;
  font-size: 16px;
  display: flex;
  align-items: center;
}

.sidebar-menu li a {
  text-decoration: none;
  color: white;
  display: flex;
  align-items: center;
  width: 100%;
}

/* Active and Hover states */
.sidebar-menu li.active,
.sidebar-menu li:hover {
  background-color: white;
}

.sidebar-menu li.active a,
.sidebar-menu li:hover a {
  color: #6D2E46; /* dark wine text */
  font-weight: bold;
}

/* Footer Section */
.sidebar-footer {
  margin-top: auto;
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.logout-btn {
  background: none;
  border: none;
  color: white;
  display: flex;
  align-items: center;
  font-size: 16px;
  cursor: pointer;
  padding: 10px 12px;
  border-radius: 8px;
  width: 100%;
}

.logout-btn:hover {
  background-color: white;
  color: #6D2E46;
  font-weight: bold;
  font-size: small;
}

.admin {
  margin-top: 10px;
  font-weight: bold;
  color: #f0d4e2; /* soft contrast on wine background */
  text-align: center;
  width: 100%;
}